import { Component, OnInit, ViewChild } from '@angular/core';
import { Router,NavigationExtras } from '@angular/router';
import { NgForm, NgModel, NgControl, FormsModule } from '@angular/forms';
import { LoopBackAuth, App_loginApi, Contractor_profileApi, App_detailsApi } from '../../../../shared/loopback-sdk';
import { ActivatedRoute } from "@angular/router";
import * as moment from 'moment'; //for date manupalation
import {ConfirmationService} from 'primeng/primeng';
import { Lightbox } from 'angular2-lightbox';
import {PaginatorModule} from 'primeng/primeng';
import {Message,MenuItem,SelectItem} from 'primeng/primeng';
import { CookieService } from 'angular2-cookie/core';
declare var window:any;
declare var $;
declare var hostname: any;
declare var sdkPrefix;
@Component({
	selector: "viewContractor",
	templateUrl: 'view-contractor.component.html',
	providers: [ConfirmationService],
})

export class ViewContractorComponent {
	result: any;
	success: boolean;
	display: boolean = false;
	public id: number;
	public role: string;
	submitted:any=false;
	backLink:any = "";
	busy:any;
	emptyMessage:String="No Result Found";
	displayConfirm: boolean = false;
	msgs: Message[] = [];
	maxDate:Date;
	loading:boolean = false;
	ifEmpty:boolean=false;

	contractorData = {};
	contractorDetails :any;
	contractordetailLabel = [];
	credentialValue :any = [];
	workGroup = [];
	contractorPhoto = '';
	contractor_status=false;
	contractorAppId:any[]=[];
	appDataArr : any = [];
	contractorApp : any = [] ;
	loginFlagToken : any;
	

	constructor(private router:Router, private _cookieService: CookieService, private _lightbox: Lightbox, private confirmationService: ConfirmationService, private appLoginApi:App_loginApi,private loopAuth:LoopBackAuth,private route: ActivatedRoute,private contratorProfileApi:Contractor_profileApi,private appDetailsApi:App_detailsApi) {
		this.id = this.route.snapshot.params['id'];
		this.maxDate = new Date();
	}

	ngOnInit(): void {
		console.log('this is called');
		let dataObj={};
		let uid=this.id;
		let dataArrObj={
			"id":this.id
		}
		this.loginFlagToken = this._cookieService.get(sdkPrefix+'id');
		
		this.loading = true;
		this.getContractorData().then((contractorResult)=>{
			console.log("contractorResult",contractorResult);
			this.loading = false;
		},(err)=>{
			this.loading = false;
		});
		this.loading = true;
		this.appDetailsApi.find({"where":{"status":1}}).subscribe(appResultdata=>{
				

			if(appResultdata.length){
				for (const key in appResultdata) {
					let appObj = {
						appId : appResultdata[key]['app_id'],
						appName : appResultdata[key]['app_name'],
					}
					
					this.appDataArr.push(appObj);
				}
			}
			console.log("app Array",this.appDataArr);
			console.log("App Result data",appResultdata);
			this.loading = false;
		},(err)=>{
			this.loading = false;
		});


		// this.busy=this.bookApi.getBooks(dataArrObj).subscribe(
		// 	data=>{
		// 		if(data.result.length>0){
		// 			if(data.result[0].status == 1){
		// 				data.result[0].status = 'Active';
		// 			}else{
		// 				data.result[0].status = 'Inactive';
		// 			}
		// 			if(data.result[0].created_at){
		// 				data.result[0].created_at = moment(data.result[0].created_at).format("DD - MMM - YYYY");
		// 			}
		// 			if(data.result[0].logo_image){
		// 				data.result[0].logo_image = data.result[0].logo_image+'?access_token='+loginFlagToken;
		// 				data.result[0].profileArr = [{ src:data.result[0].logo_image, thumb: data.result[0].logo_image}];
		// 			}
		// 			this.bookData = data.result[0];
		// 			this.getCount();
		// 		}
		// 	},
		// 	err=>{

		// 	},
		// 	()=>{

		// 	}
		// );
	}
	hideDialog(){
		this.display = false;
	}

	async submit(formData) {
		this.submitted = true;
		console.log("Form data :",formData);
		
		var contractorSubmitedValues={
			"contractor_id": this.id,
			"app_id": (formData.value.contractorAppId != undefined && formData.value.contractorAppId != null && formData.value.contractorAppId.length > 0)?formData.value.contractorAppId:[],
			"status": (formData.value.contractor_status)? 'Active':'Inactive'
		};

		console.log("contractorSubmitedValues :",contractorSubmitedValues);
	
			this.loading = true;
			this.busy = this.contratorProfileApi.edit_contractor(contractorSubmitedValues).subscribe(()=>{
				this.loading = false;
				this.result = 'success';
				this.display = true;
			},(err)=>{
				this.result = 'Something went wrong';
				this.loading = false;
				this.display = true;
			});
		


	}


	contractorStatusChanged(event){
		if(!this.contractor_status){
			this.contractorAppId = [];
		}
	}

	getContractorData(){
		return new Promise((resolve,reject)=>{
			try{
				let contractorObj = {
					"contractor_id":this.id
				}
				
				this.contratorProfileApi.get_contractor_details(contractorObj).subscribe((data)=>{
					
					this.contractorData = data.data;
					this.contractorDetails = data.data.contractorDetails;
					let appArr = data.data.apps
					
					//removing key app_id
					delete this.contractorDetails['app_id'];
					
					for (const key in this.contractorDetails) {
						this.contractordetailLabel.push(key);
						if(key == 'credentials'){
							if(this.contractorDetails[key] != ''){
								let credentialsArr = JSON.parse(this.contractorDetails[key]);
								this.credentialValue = credentialsArr;
							}
							
							
						}
						if(key == 'contractor_photo'){
							this.contractorPhoto = hostname+"/api/containers/contractorImage/download/"+this.contractorDetails[key]+'?access_token='+this.loginFlagToken;
						}
					}

					//getting workgroup data
					if(data.data.workGroup.length > 0){
						for (const key in data.data.workGroup) {
							
							let wrkGrpType = data.data.workGroup[key].member_type;
							let wrkGropNames = JSON.parse(data.data.workGroup[key].member_name);
							let wrkGrpObj = {
								memter_type : wrkGrpType,
								members_name : wrkGropNames
							}
							this.workGroup.push(wrkGrpObj);
						}
						console.log("workGroup ",this.workGroup);
					}

					//getting apps id in appay
					if(appArr.length > 0){
						for (const key in appArr) {
							
							this.contractorApp.push(appArr[key].app_id);
							
						}
						this.contractorAppId = this.contractorApp;
					}
					
					this.contractor_status = data.data.contractorDetails.status;
					
					resolve(this.contractorData);
				},()=>{
					reject(true);
				});
			}catch(err){
				reject(err);
			}	
		});
	}



	open(data,index: number): void {
		// override the default config
		this._lightbox.open(data, index, { wrapAround: true, showImageNumberLabel: true });
	}
}


